import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const SPost = styled.a`
  display: flex;
  flex-direction: column;
  gap: 15px;

  width: 100%;
  max-width: 400px;
  height: fit-content;

  position: relative;
  margin-bottom: 40px;

  text-align: left;
  cursor: pointer;
  z-index: 1;

  border-radius: 32px;  
  box-shadow: 0px 0px 0px 0px rgba(95, 176, 255, 0.17);
  
  transition: 0.45s ease-in-out;

  &:hover {
    margin: -10px 10px 43px -10px;
    box-shadow: 10px 10px 0px 5px rgba(95, 176, 255, 0.17);
  }
`;

const Img = styled.img`
  width: 100%;
  border-radius: 30px;
`;

const SText = styled(Text)`
    padding-left: 10px;
    margin-bottom: 5px;
`;

const Post = ({ img, desc, href }) => {
  return (
    <SPost href={href}>
      <Img src={img} alt="post_img" loading="lazy"/>
      <SText color="#3C3C3C" size={20}>
        {desc}
      </SText>
    </SPost>
  );
};

export default Post;
