import styled from "styled-components";

import { Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonMobile";
import Post from "./postMobile";

import Articles from "../../assets/images/Articles.svg";
import Boots from "../../assets/images/boots.png";
import { EngLoc } from "../../assets/constants/localizations";

const SPosts = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 15px;
  padding: 100px 0;
`;

const TitleBox = styled.div`
  & > h3 {
    background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 30px;

  max-width: 100%;

  text-align: left;
  padding-bottom: 15px;

  & button {
    max-width: 600px;
    margin: auto;
  }
`;

const WorkImg = styled.img`
  width: 100%;
  max-width: 400px;
  margin: auto;
`;

const Title = styled(Title3)`
  font-size: 48px;
  text-align: center;
  font-weight: 400;
`;

const PostsList = styled.div`
  display: flex;
  justify-content: center;
  gap: 20px;
  width: 100%;
  flex-wrap: wrap;
  margin-top: 44px;
  gap: 20px;
`;

const ListPosts = [
  {
    img: Boots,
    desc: "SocialJet сделает ваш контент популярным в несколько..",
  },
  {
    img: Boots,
    desc: "SocialJet сделает ваш контент популярным в несколько..",
  },
  {
    img: Boots,
    desc: "SocialJet сделает ваш контент популярным в несколько..",
  },
  {
    img: Boots,
    desc: "SocialJet сделает ваш контент популярным в несколько..",
  },
  {
    img: Boots,
    desc: "SocialJet сделает ваш контент популярным в несколько..",
  },
  {
    img: Boots,
    desc: "SocialJet сделает ваш контент популярным в несколько..",
  },
];

const Posts = ({language}) => {
  return (
    <SPosts>
      <TitleBox>
        <Title weight={300}>{language === "Ru" ? "Статьи" : EngLoc.posts.title}</Title>
        <WorkImg loading="lazy" src={Articles} />
        <a href="https://blog.socialjet.pro/">
          <Button
            width="100%"
            type="service"
            colorTxt="#1073D3"
            color="#E7F5FF"
            link={true}
          >
            {language === "Ru" ? "Перейти в блог" : EngLoc.buttons.blog}
          </Button>
        </a>
      </TitleBox>
      <PostsList>
        {ListPosts.map((post, index) => {
          if (index < 6)
            return <Post key={Math.random()} img={post.img} desc={post.desc} />;
            return null;
        })}
      </PostsList>
    </SPosts>
  );
};

export default Posts;
